.returns-page {
  padding: 0 8rem;

  .container {
    .intro {
      background-color: #bec6ff;
      padding: 2.8rem;

      * {
        background-color: #bec6ff;
        color: #000;
      }

      h3 {
        font-weight: 600;
        font-size: 3rem;
      }
    }

    .top {
      margin-top: 2rem;
      border-bottom: solid #fff 1px;
      padding-bottom: 1.5rem;

      h4 {
        border-bottom: solid #fff 1px;
        width: 50%;
        font-weight: 500;
        font-size: 1.4rem;
        padding-bottom: 0.4rem;
      }

      .ship-info {
        p {
          font-size: 1.2rem;
          margin: 0.5rem 0;

          .in-line {
            text-decoration: underline;
            text-transform: capitalize;
            color: #FE8FE8;
            border-radius: 5px;

            &:hover {
              background-color: #fe8fe8;
              color: #000;
              transition: ease-in-out .5s;
            }
          }
        }
      }

      .return-info {
        ul {
          padding: 0.5rem 1.2rem;
          list-style-type: "🧁";

          li {
            padding: 0.25rem .5rem;
          }
        }
      }

      h5 {
        color: #d0ddd9;
        font-weight: 500;
        font-size: 1rem;
        margin-top: 1rem;
      }
    }

    .bottom {
      margin-top: 1.2rem;

      .refund-form {
        margin-bottom: 1rem;

        h5 {
          font-weight: 600;
          font-size: 1.2rem;
          border-bottom: #fff solid 1px;
          width: 30%;
          padding: 0.25rem 0;
        }

        .form {
          display: flex;
          height: 50vh;

          input,
          textarea,
          select {
            background-color: #fff;
            color: #000;
            padding: 0.5rem;
            border: 0;
            outline: none;
          }

          .left {
            flex: 3;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .customer-info {
              display: flex;
              flex-direction: column;
              height: 33%;
              justify-content: space-between;

              input {
                width: 80%;
              }
            }

            .date-info {
              margin-top: 0.8rem;
              display: flex;
              justify-content: space-between;
              width: 80%;
              padding: 0.5rem 0;

              input {
                width: 40%;
              }
            }

            .refund-info {
              width: 80%;
              height: 28%;
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              margin-top: 0.5rem;

              input {
                width: 100%;
              }

              .reason {
                display: flex;
                flex-direction: column;

                label {
                  padding: 0.5rem 0;
                }

                select {
                  width: 50%;
                  font-size: 1rem;
                }
              }
            }

            button {
              align-self: flex-start;
              width: 30%;
              border: 0;
              padding: 0.5rem 1rem;
              font-size: 1.2rem;
              cursor: pointer;
              background-color: #fdd1ea;
              color: #000;
              
              &:hover {
                color: #fff;
                border: solid #fdd1ea 1px;
                background-color: inherit;
                transition: ease-in 0.5s;
              }
            }
          }

          .right {
            flex: 3;

            textarea {
              width: 100%;
              font-size: 1.3rem;
              height: 100%;
              padding: 0.5rem;
              resize: none;
            }
          }
        }
      }
    }
  }

  @media (max-width: 600px) {
    padding: 0 .8rem;

    .container {

      .intro {
        padding: .8rem;
        text-align: center;

        h3 {
          font-size: 2.1rem;
        }
      }

      .top {
        h4 {
          width: 100%;
        }

        .ship-info {
          p {
            font-size: 1.1rem;
          }
        }

        .return-info {
          font-size: .9rem;
  
          li::marker {
            font-size: .8rem;
          }
        }

        h5 {
          font-size: .8rem;
        }
      }

      .bottom {
        .refund-form {

          h5 {
            text-align: center;
            width: 100%;
            margin-bottom: .5rem;
          }

          .form {
            height: fit-content;
            flex-direction: column-reverse;

            .left {

              .customer-info {
                height: fit-content;

                input {
                  margin-bottom: .5rem;
                }
              }

              .refund-info {
                height: fit-content;

                .reason {
                  select {
                    width: 100%;
                    font-size: .8rem;
                    margin-bottom: 1rem;
                  }
                }
              }
            }

            .right {
              textarea {
                width: 80%;

                &::placeholder {
                  font-size: .8rem;
                }
              }
            }

          }
        }
      }
    }
  }
}
